@import url("https://fonts.googleapis.com/css2?family=Amaranth&family=Montserrat&family=Poppins&family=Ubuntu:wght@300;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Amaranth&family=Montserrat&family=Poppins&family=Radio+Canada:wght@300&family=Ubuntu:wght@300;500&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins";
  /* font-family: "Radio Canada"; */
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
